import React from 'react';
import NavBar from '../../components/NavBar/index.js';
import { useHistory } from "react-router";
import { useEffect } from 'react';
import "./index.css"

export default function App() {

    const history = useHistory();

    const gotoHome = () => {
        history.push('/')
    }

    useEffect(() => {
        // history.push('/')
    });

    return (
        <>
            <NavBar />
            <div class="pvl _4-do">
                <h2 class="_4-dp">Esta Página não está disponível</h2>
                <h3 class="_4-dq">O link que você seguiu pode estar quebrado ou a página pode ter sido removida.</h3>
                <img class="mvl img" src="https://static.xx.fbcdn.net/rsrc.php/v3/yp/r/U4B06nLMGQt.png" alt="" width="282" height="250" />
                <div className="div404">
                <p id="goToHome" onClick={gotoHome}>Clique aqui para voltar para a página inicial</p>
                </div>
            </div>
        </>

    )
}